<template>
  <div>
    <Loading :active.sync="isLoading" />
    <div class="text-right mt-4">
      <h3 class="float-left"></h3>
      <button
        class="btn btn-info"
        type="button"
        data-toggle="modal"
        data-target="#prodModal"
        @click="openModal(`new`)"
      >
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(prod, i) in prods" :key="prod.id">
          <td>{{ i + 1 }}</td>
          <td>{{ prod.category }}</td>
          <td>{{ prod.title }}</td>
          <td>{{ prod.origin_price | currency }}</td>
          <td>{{ prod.price | currency }}</td>
          <td>
            <span v-if="prod.enabled"></span>
            <span v-else></span>
          </td>
          <td class="text-center">
            <div class="btn-group">
              <button
                class="btn btn-info btn-sm"
                @click="openModal(`edit`, prod)"
              >
                
              </button>
              <button
                class="btn btn-outline-info btn-sm"
                @click="openModal(`delete`, prod)"
              >
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <hr />
    <div class="float-right">
      <pagination :pages="pagination" @update="getProds" />
    </div>
    <!-- prodModal -->
    <div
      class="modal fade"
      id="prodModal"
      tabindex="-1"
      aria-labelledby="prodModal"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-xl">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-5">
                <!--  -->
                <!-- <div class="form-group" v-for="i in 5" :key="i + 'img'">
                  <label for="img"></label>
                  <input
                    type="text"
                    :id="'img' + i"
                    class="form-control"
                    placeholder=""
                    v-model="tempProd.imageUrl[i - 1]"
                  />
                </div> -->
                <div class="form-group">
                  <label for="uploadImg">
                    
                    <i
                      class="fas fa-spinner fa-spin"
                      v-if="status.fileUploading"
                    />
                  </label>
                  <input
                    type="file"
                    id="uploadImg"
                    class="form-control"
                    ref="file"
                    @change="uploadImg"
                  />
                  <img :src="tempProd.imageUrl[0]" alt="" class="img-fluid" />
                </div>
              </div>
              <div class="col-sm-7">
                <div class="row">
                  <div class="col-sm-12">
                    <div class="form-group">
                      <label for="title"></label>
                      <input
                        type="text"
                        id="title"
                        class="form-control"
                        required
                        v-model="tempProd.title"
                        placeholder=""
                      />
                    </div>
                  </div>
                  <div class="col-sm-6">
                    <div class="form-group">
                      <label for="cate"></label>
                      <input
                        type="text"
                        id="cate"
                        class="form-control"
                        required
                        v-model="tempProd.category"
                        placeholder=""
                      />
                    </div>
                  </div>
                  <div class="col-sm-6">
                    <div class="form-group">
                      <label for="unit"></label>
                      <input
                        type="text"
                        id="unit"
                        class="form-control"
                        required
                        v-model="tempProd.unit"
                        placeholder=""
                      />
                    </div>
                  </div>
                  <div class="col-sm-6">
                    <div class="form-group">
                      <label for="origin_price"></label>
                      <input
                        type="number"
                        id="origin_price"
                        class="form-control"
                        required
                        v-model="tempProd.origin_price"
                        placeholder=""
                      />
                    </div>
                  </div>
                  <div class="col-sm-6">
                    <div class="form-group">
                      <label for="price"></label>
                      <input
                        type="number"
                        id="price"
                        class="form-control"
                        required
                        v-model="tempProd.price"
                        placeholder=""
                      />
                    </div>
                  </div>
                  <div class="col-sm-12">
                    <div class="form-group">
                      <label for="desc"></label>
                      <textarea
                        type="text"
                        id="desc"
                        class="form-control"
                        required
                        v-model="tempProd.description"
                        placeholder=""
                      />
                    </div>
                  </div>
                  <div class="col-sm-12">
                    <div class="form-group">
                      <label for="content"></label>
                      <vue-editor id="content" v-model="tempProd.content" />
                    </div>
                    <div class="form-group">
                      <div class="form-check">
                        <input
                          type="checkbox"
                          id="is_enabled"
                          class="form-check-input"
                          v-model="tempProd.enabled"
                        />
                        <label for="is_enabled" class="form-check-label">
                          
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button type="button" class="btn btn-info" @click="updateProd">
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- deleteModal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      aria-labelledby="deleteModal"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-info">{{ tempProd.title }}</strong>
            ()
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button type="button" class="btn btn-info" @click="removeProd">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="css">
@import "~vue2-editor/dist/vue2-editor.css";
/* Import the Quill styles you want */
@import "~quill/dist/quill.core.css";
@import "~quill/dist/quill.bubble.css";
@import "~quill/dist/quill.snow.css";
</style>

<script>
/* global $ */
import { VueEditor } from "vue2-editor/dist/vue2-editor.core";
// import Pagination from "@/components/Pagination.vue";
import pagination from "@/components/Pagination";
export default {
  components: {
    pagination,
    VueEditor
  },
  data() {
    return {
      isLoading: false,
      prods: [],
      tempProd: {
        imageUrl: []
      },
      pagination: {},
      isNew: false,
      status: {
        fileUploading: false
      }
    };
  },
  created() {
    this.getProds();
  },
  methods: {
    getProds(page = 1) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/products?page=${page}`;

      this.$http
        .get(url)
        .then(res => {
          // console.log(res);
          this.prods = res.data.data;
          this.isLoading = false;
          this.pagination = res.data.meta.pagination;
        })
        .catch(err => {
          this.$bus.$emit(
            "message:push",
            `${err.response.data.message}`,
            "danger"
          );
        });
    },
    getProd(id) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http
        .get(url)
        .then(res => {
          this.tempProd = res.data.data;
          $("#prodModal").modal("show");
          this.isLoading = false;
        })
        .catch(err => {
          console.dir(err);
          this.$bus.$emit("message:push", err.message, "danger");
          this.isLoading = false;
        });
    },
    openModal(isNew, item) {
      switch (isNew) {
        case "new":
          this.tempProd = {
            imageUrl: []
          };
          this.isNew = true;
          $("#prodModal").modal("show");
          break;
        case "edit":
          this.isNew = false;
          // this.tempProd = JSON.parse(JSON.stringify(item));
          // console.log(item);
          this.getProd(item.id);
          // this.$refs.prodModal.getProd(this.tempData.id);
          // $("#prodModal").modal("show");
          break;
        case "delete":
          this.tempProd = { ...item };
          $("#deleteModal").modal("show");
          break;
      }
    },
    updateProd() {
      this.isLoading = true;
      let url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product`;
      let httpMethod = "post";

      if (!this.isNew) {
        url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProd.id}`;
        httpMethod = "patch";
      }

      this.$http[httpMethod](url, this.tempProd)
        .then(() => {
          $("#prodModal").modal("hide");
          this.$bus.$emit(
            "message:push",
            `${this.isNew ? "" : ""}`,
            "success"
          );
          this.isLoading = false;
          this.getProds();
        })
        .catch(err => {
          console.dir(err);
          this.$bus.$emit(
            "message:push",
            `${this.isNew ? "" : ""}`,
            "danger"
          );
          this.isLoading = false;
        });
    },
    uploadImg() {
      const uploadedImg = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append("file", uploadedImg);
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/storage`;
      this.status.fileUploading = true;
      this.$http
        .post(url, formData, {
          headers: {
            "Content-type": "multipart/form-date"
          }
        })
        .then(res => {
          this.status.fileUploading = false;
          if (res.status === 200) {
            this.tempProd.imageUrl.push(res.data.data.path);
          }
          this.$bus.$emit("message:push", "", "success");
        })
        .catch(() => {
          this.$bus.$emit("message:push", "", "danger");
          this.status.fileUploading = false;
        });
    },
    removeProd() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProd.id}`;
      this.$http
        .delete(url)
        .then(res => {
          console.log(res);
          $("#deleteModal").modal("hide");
          this.isLoading = false;
          this.$bus.$emit("message:push", "", "success");
          this.getProds();
        })
        .catch(err => {
          console.dir(err);
          $("#deleteModal").modal("hide");
          this.isLoading = false;
          this.$bus.$emit("message:push", "", "danger");
        });
    }
  }
};
</script>
